* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.Board {
  font-family: sans-serif;
  white-space: nowrap;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.Column {
  display: inline-block;
  vertical-align: top;
  width: 225px;
  padding: 10px 15px;
  border-radius: 5px;
  margin-right: 20px;
  background: #f6f7f8;
  color: #9d48ec;
  margin-top: 10px;
}

.Column__title {
  font-size: 18px;
  margin-bottom: 10px;
}

.Card {
  padding: 15px;
  background: #9d48ec;
  color: #f4fffd;
  border-radius: 5px;
  margin-bottom: 10px;
  cursor: move;
}

.Card--spacer,
.Card--dragging {
  opacity: 0;
}

.TextForm__input {
  display: block;
  background: #f4fffd;
  width: 100%;
  border-radius: 3px;
  border: none;
  font-size: 16px;
  padding: 5px;
  margin-bottom: 5px;
  opacity: 0.7;
}

.TextForm__input:focus {
  opacity: 1;
}
.html,body{
  background-color: rgb(211, 184, 253);
}



